<template>
    <div class="header">
        <el-menu :default-active="activeIndex" class="menu" mode="horizontal" @select="handleSelect" :background-color="bgColor" text-color="#fff" :active-text-color="activeColor">
            <el-menu-item v-for="item of list" :key="item.id" :index="item.id.toString()">{{item.id}}-{{item.title}}</el-menu-item>
        </el-menu>
    </div>
</template>

<script>
    import {
        mapState
    } from 'vuex'
    export default {
        name: "layoutHeader",
        props: {
            list: Array
        },
        data() {
            return {
                activeIndex: localStorage.menuActive || '1',
            }
        },
        computed: {
            ...mapState(['bgColor', "activeColor"])
        },
        methods: {
            handleSelect(key) {
                // console.log(key, keyPath);
                localStorage.menuActive = key
                this.$emit('changeAside', key)
            }
        },
        // mounted() {
        //     console.log(this.$store.state.bgColor)
        // }
    }
</script>

<style lang="stylus" scoped>
    @import '~styles/varibles.styl';

    .menu {
        background-color: $headerColor;
        color: #fff;
    }
</style>