<template>
	<!-- <el-container class="container">
        <el-aside class="aside" width="300px">
            <layoutAside :activeId="activeId"></layoutAside>
        </el-aside>
        <el-container>
            <el-header class="head">
                <layoutHeader :list="headMenus" @changeAside="handleactiveIdChange"></layoutHeader>
            </el-header>
            <el-main class="mian">Main</el-main>
        </el-container>
    </el-container> -->

	<el-container class="container">
		<el-header class="head">
			<layoutHeader :list="headMenus" @changeAside="handleactiveIdChange"></layoutHeader>
		</el-header>
		<el-container>
			<el-aside class="aside" width="200px">
				<layoutAside :activeId="activeId"></layoutAside>
			</el-aside>
			<el-main>Main</el-main>
		</el-container>
	</el-container>
</template>

<script>
	import layoutHeader from './components/Header'
	import layoutAside from './components/Aside'
	export default {
		components: {
			layoutHeader,
			layoutAside
		},
		data() {
			return {
				headMenus: [],
				asideMenus: [],
				menus: [],
				activeId: "1"
			}
		},
		methods: {
			getMenus() {
				this.$http.get('/api/menu2.json').then(res => {
					res = res.data.menus
					// console.log(res)
					this.headMenus = res.filter(m => {
						return m.parentId == 0
					})
					// console.log(this.headMenus)
				}).catch(error => {
					if (error.response) {
						console.log(error.response.data);
						console.log(error.response.status);
						console.log(error.response.headers);
					} else if (error.request) {

						console.log(error.request);
					} else {
						console.log('Error', error.message);
					}
					console.log(error.config);
				})
			},
			handleactiveIdChange(val) {
				// console.log(val)
				this.activeId = val
			}
		},
		mounted() {
			this.getMenus()
		}
	}
</script>

<style scoped lang="stylus">
	@import url();

	.container {
		height: 100vh;

		.aside {
			background: #ddd;
		}

		.head {
			background: #b3c0d1;
			padding: 0;
		}

		.mian {
			background: #f5f5f5;
		}
	}
</style>
