<template>
    <div class="aside">
        <!-- <el-menu default-active="2" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b"> -->
        <el-menu default-active="1" class="el-menu-vertical-demo" :background-color="bgColor" text-color="#fff" :active-text-color="activeColor">
            <!-- 
            <el-submenu index="1">
                <template slot="title">
                    <span></span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="1-1">1</el-menu-item>
                    <el-menu-item index="1-2">2</el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-menu-item index="2">
                <span slot="title"></span>
            </el-menu-item> 
            -->
            <template v-for="item of list[activeId]">
                <el-menu-item :index="item.id.toString()" :key="item.id" v-if="!item.children">
                    <span slot="title"></span>
                </el-menu-item>
                <el-submenu :index="item.id.toString()" :key="item.id" v-if="item.children">
                    <template slot="title">
                        <span>{{item.title}}</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item :index="child.id.toString()" v-for="child of item.children" :key="child.id">{{child.title}}</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
            </template>
        </el-menu>
        <div>
            activeId
            {{activeId}}
        </div>
    </div>
</template>

<script>
    import {
        mapState
    } from 'vuex'
    export default {
        name: "layoutAside",
        props: {
            activeId: [String, Number]
        },
        data() {
            return {
                list:[
                    [
                        {id:1,title:"1",children:[
                            {id:"1-1",title:"1.1"},
                            {id:"1-2",title:"1.2"}
                        ]},
                        {id:2,title:"1"}
                    ],
                    [
                        {id:1,title:"2",children:[
                            {id:"2-1",title:"2.1"},
                            {id:"2-2",title:"2.2"}
                        ]},
                        {id:2,title:"2"}
                    ],
                    [
                        {id:1,title:"3"},
                        {id:2,title:"3"}
                    ],
                    [
                        {id:1,title:"4"},
                        {id:2,title:"4"}
                    ],
                    [
                        {id:1,title:"5"},
                        {id:2,title:"5"}
                    ],
                    [
                        {id:1,title:"6"},
                        {id:2,title:"6"}
                    ],
                    [
                        {id:1,title:"7"},
                        {id:2,title:"7"}
                    ]
                ]
            }
        },
        computed: {
            ...mapState(['bgColor', "activeColor"])
        },
        methods: {

        }
    }
</script>

<style lang="stylus" scoped>
    // @import '~styles/varibles.styl';
</style>